.pokemonsTitle {
  font-size: 36px;
  line-height: 44px;
  letter-spacing: 0.04em;
  color: #ffffff;
  text-transform: uppercase;
  margin-top: 50px;
}

.pokemonsContainer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 25px;
  align-items: center;
  justify-items: center;
}

.pokemonsSubtitle {
  margin-bottom: 30px;
}

.noPokemons {
  display: flex;
}
