.App {
  text-align: center;
}

* {
  font-family: Montserrat;
}
body {
  padding-bottom: 30px;
  background-color: #000829;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #000829;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  user-select: none;
}
.title {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  line-height: 29px;
  letter-spacing: 0.1em;
  color: #ffffff;
  margin-bottom: 150px;
  margin-top: 150px;
}

.inputsPokemon {
  border: none;
  border-bottom: solid 1px #62d0ff;
  background-color: transparent;
  padding-left: 3px;
  height: 35px;
  font-size: 24px;
  color: white;
  width: 100%;
  margin-bottom: 21px;
}

.inputsPokemon::-webkit-input-placeholder {
  text-transform: uppercase;
  display: inline;
  font-size: 12px;
  letter-spacing: 0.04em;
  color: white;
  opacity: 0.6;
  transition: 0.5s;
}

.inputsPokemon:focus {
  border: none;
  border-bottom: solid 1px #ff062e;
  outline: none;
}

.inputsPokemon:focus::placeholder {
  transition: 0.5s;
  opacity: 0.3;
}

.buttons {
  width: 178px;
  height: 48px;
  background: #ff062e;
  border: 1px solid #ff062e;
  box-sizing: border-box;
  border-radius: 10px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 13px;
  line-height: 18px;
  letter-spacing: 0.04em;
  cursor: pointer;
  color: #ffffff;
  outline: none;
  text-transform: uppercase;
  margin: 0 auto;
}
.buttons a {
  color: #ffffff;
  text-decoration: none;
}
.buttons:hover {
  box-shadow: 0px 0px 30px rgba(255, 6, 46, 0.86);
}

.buttons:active {
  background: #ffffff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  color: #ff062e;
  border: none;
}

.buttons:disabled {
  background: rgba(255, 6, 46, 0.35);
  border-radius: 10px;
  border: none;
  cursor: not-allowed;
}

.buttonContainer {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}
