.createImage {
  width: 200px;
}

.ataquesContainer {
  display: flex;
  margin-bottom: 20px;
}

.cardAttack {
  background: rgba(98, 208, 255, 0.2);
  border-radius: 10px;
  height: 30px;
  padding: 15px;
  text-transform: uppercase;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  user-select: none;
  margin-right: 10px;
}

.cardAttackActivate {
  background: rgba(98, 208, 255, 0.7);
}
