.containerAbsolute {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
}

.content {
  padding: 60px;
  width: 500px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 140.4%;
  text-align: center;
  color: #000829;
  background-color: white;
  border-radius: 10px;
}

.errorTitle {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #ff062e;
}

.errorStatus {
  margin-top: 25px;
}

.errorMessage {
  font-weight: bold;
  margin-top: 20px;
  height: 60px;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-all;
}
