.navbarContainer {
  top: 0px;
  left: 0px;
  width: 100%;
  height: 70px;
  background: rgba(98, 208, 255, 0.15);
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbarContainer a {
  text-decoration: none;
}

.link {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  line-height: 12px;
  text-align: center;
  letter-spacing: 0.04em;
  color: #62d0ff;
  padding-top: 15px;
  text-decoration: none;
  font-size: 20px;
  border: none;
  border-top: 1px solid #62d0ff;
  margin-right: 20px;
}

.link:hover {
  font-weight: bold;
  border-top: 2px solid #62d0ff;
}
